@use '../utils' as *;

/*----------------------------------------*/
/*  20. project CSS START
/*----------------------------------------*/
.project{
    &__active{
        margin:0 15px;
        @media #{$xs}{
            margin: 0;
        }
    }
    &__item{
        & img{
            width: 100%;
        }
    }
}

.project-slider-dots {
    position: unset !important;
	text-align: center;
    margin-top: 60px;
    & .swiper-pagination-bullet {
        width: 18px;
        height: 18px;
        display: inline-block;
        border-radius: 50%;
        border: 1px solid var(--it-theme-1);
        background-color: transparent;
        opacity: 1;
        margin: 0 5px;
        transition: .3s;
    }
    & .swiper-pagination-bullet-active {
        background-color: var(--it-theme-1);
    }
}

.project-2{
    &__plr{
    padding: 0 200px;
    padding-top:150px;
    padding-bottom:120px;
        @media #{$xxxl}{
            padding: 0 100px;
            padding-top:120px;
            padding-bottom:120px;
        }
        @media #{$xxl,$xl,$lg}{
            padding:0 30px;
            padding-top:120px;
            padding-bottom:120px;
        }
        @media #{$md,$xs}{
            padding:0;
            padding-top:70px;
            padding-bottom:70px;
        }
    }
    &__shape-1{
        position: absolute;
        left: 0;
        top: 22%;
        animation: tpupdown 0.8s infinite alternate;
        @media #{$xl}{                
            top: 4%;
        }
    }
    &__shape-2{
        position: absolute;
        left: 0;
        bottom: 16%;
        animation: rotate2 15s linear infinite;
        @media #{$xl}{                
            left: -1%;
            bottom: 0%;
        }
    }
    &__shape-3{
        position: absolute;
        right:0;
        top: 16%;
        animation: jumpTwo 4s infinite linear;
        @media #{$xl}{                
            right: 0%;
            top: 7%;
        }
    }
    &__shape-4{
        position: absolute;
        right: 0;
        bottom: 3%;
        animation: tpupdown 0.8s infinite alternate;
        @media #{$xl}{ 
            right: -6%;
            bottom: -2%;
        }
    }
    &__button-box{
        padding-bottom: 60px;
        & .react-tabs__tab--selected {
            & button{
                color: var(--it-common-black);
                background-color: var(--it-theme-1);
            }

        }
        & button{
            font-weight: 600;
            font-size: 18px;
            color: var(--it-common-black);
            margin:0 5px;
            background-color: transparent;
            padding:10px 20px;
            border-radius: 30px;
            padding: 6px 24px;
            font-family: 'Poppins', sans-serif;
            transition: .3s;
            @media #{$md}{
                font-size: 17px;
                padding: 6px 15px;
            }
            @media #{$xs}{
                font-size: 13px;
                padding: 6px 17px;
                margin-bottom: 15px;
            }
            @media #{$sm}{
                font-size: 17px;
            }
            &.active{
                background-color: var(--it-theme-1);
            }
            &:hover{
                background-color: var(--it-theme-1);
            }
        }

    }
    &__thumb{
        & img{
            border-radius: 10px;
            width: 100%;
        }
        position: relative;
        &::after{
            position: absolute;
            content: "";
            top: 0;
            left: 0;
            height: 100%;
            width: 100%;
            background-color: rgba(41, 41, 48, 0.488);
            opacity: 0;
            visibility: hidden;
            transition: 0.3s;
            transform: scale(0.5);
            border-radius: 10px;
        }
    }
    &__item{
        margin-bottom: 30px;
        &:hover{
            & .project-2__thumb{
                &::after{
                    opacity: 1;
                    visibility: visible;
                    transform: scale(1);
                }
            }
            & .project-2__img-icon{
                opacity: 1;
                visibility: visible;              
            }
            & .project-2__img-text{
                opacity: 1;
                visibility:visible;                
            }
        }
    }
    &__img-text{
        position: absolute;
        left: 0;
        bottom: 0;
        margin: 30px;
        z-index: 4;    
        opacity: 0;
        visibility: hidden;
        transition: .3s;    
        @media #{$lg}{
            margin: 15px;
        }
        & h4{
            color: var(--it-common-white);
            font-weight: 700;
            font-size: 22px;
            transition: .3s;
            @media #{$lg}{
                font-size: 18px;
            }
            @media #{$md}{
                font-size: 20px;
            }
            @media #{$xs}{
                font-size: 19px;
            }
            @media #{$sm}{
                font-size: 21px;
            }
            &:hover{
                color: var(--it-theme-1);
            }
        }
        & i{
            font-weight: 400;
            font-style: normal;
            color: var(--it-common-white);
            font-size: 16px;
            & span{
                color: var(--it-theme-1);
            }
            @media #{$lg}{
                font-size: 14px;
            }
            @media #{$xs}{
                font-size: 12px;
            }
            @media #{$sm}{
                font-size: 15px;
            }
        }
    }
    &__img-icon{
        position: absolute;
        top: 0;
        right: 0;
        margin: 20px;
        z-index: 4;
        opacity: 0;
        visibility: hidden;
        transition: .3s;
        & a {
            height: 55px;
            width: 55px;
            background-color: var(--it-theme-1);
            display: inline-block;
            line-height: 58px;
            text-align: center;
            border-radius: 5px;
            @media #{$lg}{
                height: 40px;
                width: 40px;
                line-height: 46px;
            }
            & i {
                font-weight: 500;
                font-size: 20px;
                color: var(--it-common-black);
                @media #{$lg}{
                    font-size: 18px;
                }
            }
        }
    }
    &__button-box-2{
        & .react-tabs__tab--selected {
            & button{
                color: var(--it-common-black);
                background-color: var(--it-theme-1);
            }

        }
        & button{
            color: var(--it-common-white);
            transition: .3s;
            &:hover{
                color: var(--it-common-black);
            }
        }
    }
    &__plr-2{
        padding: 0 200px;
        padding-top:150px;
        padding-bottom:120px;
        @media #{$xxxl}{
            padding:0 100px;
            padding-top:150px;
            padding-bottom:120px;
        }
        @media #{$xxl}{
            padding:0 100px;
            padding-top:150px;
            padding-bottom:120px;
        }
        @media #{$xl,$lg,$md}{
            padding: 0 30px;
            padding-top:150px;
            padding-bottom:120px;
        }
        @media #{$xs}{
            padding: 0;
            padding-top:150px;
            padding-bottom:120px;
        }
    }
    &__plr-3{
        padding: 0 200px;
        padding-top:100px;
        padding-bottom:100px;
        @media #{$xxxl}{
            padding:0 100px;
            padding-top:100px;
            padding-bottom:100px;
        }
        @media #{$xxl}{
            padding:0 100px;
            padding-top:100px;
            padding-bottom:100px;
        }
        @media #{$xl,$lg,$md}{
            padding: 0 30px;
            padding-top:100px;
            padding-bottom:100px;
        }
        @media #{$xs}{
            padding: 0;
            padding-top:100px;
            padding-bottom:100px;
        }
    }

}
.project-3{
    &__plr{
        padding: 0 200px;
        @media #{$xxxl}{
            padding:0 100px;
        }
        @media #{$xxl}{
            padding:0 100px;
        }
        @media #{$xl,$lg,$md}{
            padding: 0 30px;
        }
        @media #{$xs}{
            padding: 0;
        } 
    }
    &__space{
        padding-top: 120px;
        padding-bottom: 100px;
    }
    &__text{
        padding-right: 60px;
        padding-bottom: 10px;
    }
    &__project-tab{
        margin-bottom: 60px;
        @media #{$md,$xs}{    
            margin-bottom: 30px;
        }
        & ul{
            & li{
                &.react-tabs__tab--selected {
                    & button {
                        background-color: var(--it-theme-1);
                        color: var(--it-common-black); 
                    }
                }
                & button{
                    font-size: 18px;
                    font-weight: 600;
                    font-family: 'Poppins', sans-serif;
                    color: var(--it-common-black);
                    background-color: transparent;
                    padding: 5px 24px;
                    border-radius: 30px;
                    margin-right: 10px;
                    transition: .3s;
                    @media #{$xxl}{    
                        padding: 5px 15px;
                    }
                    @media #{$xl}{    
                        padding: 5px 14px;
                        margin-right: 5px;
                    }
                    @media #{$lg}{    
                        padding: 3px 10px;
                        margin-right: 5px;
                        font-size: 15px;
                    }
                    @media #{$xs}{    
                        margin-bottom: 15px;
                        font-size: 15px;
                        padding: 7px 18px;
                        margin-right: 6px;
                    }
                    @media #{$sm}{    
                        font-size: 16px;
                    }
                    &:hover{
                        background-color: var(--it-theme-1);
                        color: var(--it-common-black);
                    }         
                }
            }      
        }
    }
    &__item{
        &:hover{
            & .project-3__icon{
                & a {
                    & i{
                        color: var(--it-common-blue-3);
                    }
                    &::before{
                        background-color: var(--it-common-blue);
                    }
                }
            }
        }
    }
    &__main-img{
        & img{
            width: 100%;
            border-radius: 10px;
        }
    }
    &__title-sm{
        font-weight: 700;
        font-size: 22px;
        margin-bottom: 0;
        padding-bottom: 6px; 
        transition: .3s;
        @media #{$lg}{
            font-size: 17px;
        }
        @media #{$md}{
            font-size: 15px;
        }
        @media #{$xs}{
            font-size: 14px;
        }
        @media #{$sm}{
            font-size: 18px;
        }
        &:hover{
            color: var(--it-common-blue-4);
        }
    }
    &__content-text{
        & span{
            font-size: 14px;
            @media #{$md}{
                font-size: 13px;
            }
        }
    }
    &__content{
        padding:30px 25px;
    }
    &__icon{
        padding-right: 45px;
        @media #{$md,$xs}{
            padding-right: 10px;
        }
        @media #{$sm}{
            padding-right: 15px;
        }
        & a {
            position: relative;
            & i{
                font-size: 24px;
                color: var(--it-common-black);
                font-weight: 500;
                position: relative;
                z-index: 2;
                transform: translateY(2px);
                margin-left: 19px;
                transition: .3s;
                @media #{$md,$xs}{
                    font-size: 19px;
                    margin-left: 17px;
                }
                @media #{$sm}{
                    font-size: 20px;
                    margin-left: 17px;
                    transform: translateY(1px);
                }
            }
            &::before{
                content: '';
                height: 30px;
                width: 30px;
                background-color: var(--it-common-black-4);
                border-radius: 50%;
                position: absolute;
                top: 50%;
                transform: translateY(-50%);
                left: 0;
                transition: .3s;
                @media #{$md,$xs}{
                    height: 25px;
                    width: 25px;
                }
            }
        }
    }
    &__section-box {
        margin-bottom: 50px;
        @media #{$md}{
            margin-bottom: 50px;
        }
        @media #{$xs}{
            margin-bottom: 50px;
        }
    }
    &__space-2{
        padding: 100px 0;
    }
}
