                padding-right: 15px;
            }
            @media #{$xs}{
                font-size: 12px;
            }
            @media #{$sm}{
                font-size: 16px;
                padding-right: 30px;
            }
        }
    }
    &__category{
        margin-bottom: 20px;
        & span{
            font-size: 15px;
            color: var(--it-common-black);
            background-color: #fcace9;
            padding: 6px 28px;
            border-radius: 30px;
            @media #{$md}{
                padding: 5px 20px;
            }
            @media #{$xs}{
                font-size: 14px;
                padding: 4px 17px;
            }
            @media #{$sm}{
                font-size: 15px;
                padding: 5px 25px;
            }
        }
        &.category-color-2{
            & span{
                background-color: var(--it-common-blue-4);
            }
        }
        &.category-color-3{
            & span{
                background-color: #59c9da;
            }
