    }
    &__link{
        padding-bottom: 40px;
        @media #{$xs}{
            padding-bottom: 20px;
        }
        &.link-color-2{
            & span{
                color: var(--it-common-blue-4);
            }
        }
        &.link-color-3{
            & span{
                color:#59c9da;
             }
        }
        & span{
            font-size: 16px;
            font-weight: 500;
            color: #fcace9;
            @media #{$md}{
                font-size: 15px;
            }
            @media #{$xs}{
                font-size: 14px;
            }
            @media #{$sm}{
                font-size: 16px;
            }
            &:hover {
                & i {
                    animation: tfLeftToRight 0.4s forwards;
                  }
            }
            & i{
                padding-left: 10px;
            }

        }
    }
}


.project__section-arrow{
    position: relative;
    & .swiper-button {
        width: auto;
        position: unset;
        &:after {
            display: none;
        }
    }
    & button{
        height: 50px;
        width: 50px;
        background-color: var(--it-theme-1);
        border-radius: 50%;
        line-height: 53px;
        text-align: center;
        color: var(--it-common-black);
        margin: 0px 10px;
        transition: .4s;
        &:hover{
            transform: scale(1.2);
        }
    }
}
.project-details{
    &__plr{
        padding: 0 200px;
        @media #{$xxxl}{
            padding:0 100px;
        }
        @media #{$xxl}{
            padding:0 100px;
        }
        @media #{$xl,$lg,$md}{
            padding: 0 30px;
        }
        @media #{$xs}{
            padding: 0;
        }
    }
    &__right{
        padding-bottom: 5px;
        margin-left: 240px;
        @media #{$lg}{                
            margin-left: 90px;
        }
        @media #{$md,$xs}{                
            margin-left:0;
        }
