                text-decoration: transparent;
                &:hover{
                    text-decoration: underline;
                }
                & i {
                    position: absolute;
                    top: 50%;
                    transform: translateY(-50%);
                    left: 0;
                    font-weight: 500;
                    color: var(--it-common-blue-3);
                    font-size: 16px;
                }

                & span {
                    font-weight: 400;
                    font-size: 17px;
                    margin-right: 5px;
                    @media #{$md}{
                        font-size: 15px;
                    }
                    @media #{$lg,$xs} {
                        font-size: 15px;
                    }
                }
            }
        }
    }
    &__img{
        & img{
            width: 100%;
        }
    }
}
.project-2{
    &__wrapper{
        display: flex;
        flex-wrap: wrap;
        & .project-2__item{
            padding: 0 15px;
        }
    }
}
.project-3{
    &__inner-wrapper{
        & .project-3__item{
            margin-bottom: 50px;
            @media #{$xs}{
                margin-bottom: 30px;
            }
            &:hover{
                & .project-3__main-img{
                    &::after{
                        opacity: 1;
                        visibility: visible;
                        height: 19%;
                        @media #{$md}{
                            height: 30%;
                        } 
                        @media #{$xs}{
                            height: 37%;
                        } 
                        @media #{$sm}{
                            height: 24%;
                        } 
                    }
                }
                & .project-3__img-icon{
                    opacity: 1;
                    visibility: visible;              
                }
                & .project-3__img-text{
                    opacity: 1;
                    visibility:visible;                
                }
            }
        }
        & .project-3__main-img{
            position: relative;
            &::after{
                position: absolute;
                content: "";
                bottom:0;
                left: 0;
                height: 0;
                width: 100%;
                background-color: rgba(41, 41, 48, 0.488);
                transition: 0.3s;
                opacity: 0;
                visibility:hidden; 
            }
        }
        & .project-3__img-icon{
            position: absolute;
            bottom: 0;
            right: 0;
            margin: 28px;
            z-index: 4;
            transition: .3s;
            opacity: 0;
            visibility:hidden; 
            @media #{$xl}{
                margin: 25px;
            }  
            @media #{$lg}{
                margin: 21px;
            }  
            @media #{$xs}{
                margin: 15px;
            }   
        & a {
            height: 90px;
            width: 90px;
            background-color: var(--it-theme-1);
            display: inline-block;
            line-height: 110px;
            text-align: center;
            border-radius: 5px;
            @media #{$lg}{
                height: 70px;
                width: 70px;
                line-height: 82px;
            }  
            @media #{$md}{
                height: 70px;
                width: 70px;
                line-height: 82px;
            }  
            @media #{$xs}{
                height: 40px;
                width: 40px;
                line-height: 46px;
            }  
            @media #{$sm}{
                height: 50px;
                width: 50px;
                line-height: 56px;
            } 
            & i {
                font-weight: 500;
                font-size: 40px;
                color: var(--it-common-black);
                @media #{$lg}{
                    font-size: 30px;
                } 
                @media #{$md}{
                    font-size: 30px;
                } 
                @media #{$xs}{
                    font-size: 20px;
                } 
            }
        }
        }
        & .project-3__img-text{
            position: absolute;
            left: 0;
            bottom: 0;
            margin: 30px;
            z-index: 4;    
            transition: .3s; 
            opacity: 0;
            visibility:hidden; 
            @media #{$xs}{
                margin: 15px;
            }   
        & h4{
            color: var(--it-common-white);
            font-weight: 700;
            font-size: 22px;
            transition: .3s;
            @media #{$xs}{
                font-size: 16px;
            }
            &:hover{
                color: var(--it-theme-1);
            }
        }
        & i{
            font-weight: 400;
            font-style: normal;
            color: var(--it-common-white);
            font-size: 16px;
            & span{
                color: var(--it-theme-1);
            }
            @media #{$xs}{
                font-size: 12px;
            }
        }
        }
    }
}